import * as Yup from 'yup';

export const editPasswordSchema = Yup.object().shape({
  password: Yup.string()
    .min(8, '–ü–∞—Ä–æ–ª—å –º–∞—î –±—É—Ç–∏ –Ω–µ –º–µ–Ω—à–∏–π 8 —Å–∏–º–≤–æ–ª—ñ–≤')
    .max(20, '–ü–∞—Ä–æ–ª—å –º–∞—î –±—É—Ç–∏ –Ω–µ –¥–æ–≤—à–∏–π 20 —Å–∏–º–≤–æ–ª—ñ–≤')
    .required('–í–≤–µ–¥—ñ—Ç—å —Å—Ç–∞—Ä–∏–π –ø–∞—Ä–æ–ª—å, –±—É–¥—å –ª–∞—Å–∫–∞')
    .trim(),
  newPassword: Yup.string()
    .notOneOf(
      [Yup.ref('password'), null],
      '–°—Ç–∞—Ä–∏–π —ñ –Ω–æ–≤–∏–π –ø–∞—Ä–æ–ª—ñ –æ–¥–Ω–∞–∫–æ–≤—ñ'
    )
    .min(8, '–ü–∞—Ä–æ–ª—å –º–∞—î –±—É—Ç–∏ –Ω–µ –º–µ–Ω—à–∏–π 8 —Å–∏–º–≤–æ–ª—ñ–≤')
    .max(20, '–ü–∞—Ä–æ–ª—å –º–∞—î –±—É—Ç–∏ –Ω–µ –¥–æ–≤—à–∏–π 20 —Å–∏–º–≤–æ–ª—ñ–≤')
    .required('–í–≤–µ–¥—ñ—Ç—å –ø–∞—Ä–æ–ª—å, –±—É–¥—å –ª–∞—Å–∫–∞')
    .trim(),
  newPasswordConfirmation: Yup.string()
    .oneOf([Yup.ref('newPassword'), null], '–ù–æ–≤–∏–π –ø–∞—Ä–æ–ª—å –Ω–µ –ø—ñ–¥—Ç–≤–µ—Ä–¥–∂–µ–Ω–æ')
    .required('–ü—ñ–¥—Ç–≤–µ—Ä–¥—ñ—Ç—å –Ω–æ–≤–∏–π –ø–∞—Ä–æ–ª—å, –±—É–¥—å –ª–∞—Å–∫–∞')
    .trim(),
});
